function MyInfoTab() {
  const TabContent = ({ title, content }: any) => (
    <div className="tabcontent">
      <h3 className="title-info">{title}</h3>
      <p className="title-par">{content}</p>
    </div>
  );
  const [active, setActive] = useState(0);

  const openTab = (e: any) => setActive(+e.target.dataset.index);

  return (
    <div className="tab-flex">
      <div className="tab">
        {items.map((n: any, i: any) => (
          <button
            className={`tablinks ${i === active ? "active" : ""}`}
            onClick={openTab}
            data-index={i}
          >
            {n.title}
          </button>
        ))}
      </div>
      <div className="info-tab">
        {items[active] && <TabContent {...items[active]} />}
      </div>
    </div>
  );

  //   return (
  //     <div className="navbar">
  //       <button type="button">S K I L L S</button>
  //       <button type="button">W o r k</button>
  //       <button type="button">E D U C A T I O N</button>
  //       <button type="button">My w o r k s</button>
  //       <button type="button">About my self</button>
  //       <button type="button">P r o f i l e</button>
  //     </div>
  //   );
}

export default MyInfoTab;
